  form = this.fb.group({
    image: [],
    name: this.fb.group({
      en: ['', Validators.required],
      ar: ['', Validators.required],
    }),
    price: [''],
    description: this.fb.group({
      en: ['', Validators.required],
      ar: ['', Validators.required],
    }),
    category: this.fb.group({
      _id: [],
      name: this.fb.group({
        en: [],
        ar: [],
      }),
    }),
    productAttributes: this.fb.array([]),
  });

  ngOnInit(): void {
    this.getbaseCategoriesList();
    // this.getproductAttributesList();
  }

  // Start select dropdown
  // Start select dropdown
  selectBaseCategoriesList: InputSelectItem[];
  subCategories = [];
  selectedCategory = {};
  getbaseCategoriesList() {
    this.categoriesService.getBaseCategories();

    this.categoriesService.baseCategories.subscribe((data) => {
      if (data) {
        this.selectBaseCategoriesList = data.map((x) => {
          return {
            _id: x['_id'],
            nameEn: `${x['name']['en']}`,
            nameAr: `${x['name']['ar']}`,
          };
        });
      }
    });
  }
  selectBaseCategory(event) {
    this.selectedCategory = {};
    this.subCategories = [];
    this.resetProductAttributes();

    if (event) {
      this.selectedCategory = {};
      this.selectedCategory[0] = event;

      this.categoriesService.getOne(event._id).subscribe(
        (res) => {
          this.toastr.info(res['message'], 'Info');
          this.subCategories = [];
          if (res['data'].categories) {
            if (res['data'].categories.length > 0) {
              this.subCategories[0] = res['data'].categories.map((x) => {
                return {
                  _id: x['_id'],
                  nameEn: `${x['name']['en']}`,
                  nameAr: `${x['name']['ar']}`,
                };
              });
            }
          }
          if (res['data'].productAttributes) {
            console.log(res['data'].productAttributes);
            this.getproductAttributesList(res['data'].productAttributes);
          }
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );
    } else {
      this.selectedCategory = {};
      this.subCategories = [];
      this.resetProductAttributes();
    }
  }
  // End select dropdown

  selectSubCategory(fieldIndex, event) {
    this.resetProductAttributes();
    // delete all next selected categories to index
    for (const key in this.selectedCategory) {
      if (key > fieldIndex + 1) {
        delete this.selectedCategory[key];
      }
    }

    // delete all next subcategories
    this.subCategories.splice(fieldIndex + 1);

    if (event) {
      // selected category of index = event
      this.selectedCategory[fieldIndex + 1] = event;

      this.categoriesService.getOne(event._id).subscribe(
        (res) => {
          this.toastr.info(res['message'], 'Info');
          if (res['data'].categories) {
            if (res['data'].categories.length > 0) {
              this.subCategories[fieldIndex + 1] = res['data'].categories.map(
                (x) => {
                  return {
                    _id: x['_id'],
                    nameEn: `${x['name']['en']}`,
                    nameAr: `${x['name']['ar']}`,
                  };
                }
              );
            }
          }
          if (res['data'].productAttributes) {
            console.log(res['data'].productAttributes);
            this.getproductAttributesList(res['data'].productAttributes);
          }
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );
    } else {
      // selected category of index = event
      delete this.selectedCategory[fieldIndex + 1];
    }
  }
  // End select dropdown

  // Start select dropdown
  // dropdown lists for product attributes
  selectProductAttributesListValues: [{ values: InputSelectItem[] }] = [
    { values: [] },
  ];
  selectedProductAttribute: [{ values: InputSelectItem[] }] = [{ values: [] }];
  // selectProductAttributesListValues: { values: InputSelectItem[] }[] = [];
  // selectedProductAttribute: { values: InputSelectItem[] }[] = [];
  selectProductAttribute(event, productAttributeIndex) {
    this.selectedProductAttribute[productAttributeIndex].values = event;
  }
  resetProductAttributes() {
    this.selectProductAttributesListValues = [{ values: [] }];
    this.selectedProductAttribute = [{ values: [] }];
    const productAttributes = this.form.get('productAttributes') as FormArray;
    while (productAttributes.length !== 0) {
      productAttributes.removeAt(0);
    }
  }
  getproductAttributesList(attributes) {
    const productAttributes = this.form.get('productAttributes') as FormArray;
    for (let i = 0; i < attributes.length; i++) {
      this.selectedProductAttribute[i] = { values: [] };
      this.selectProductAttributesListValues[i] = { values: [] };
      // this.selectedProductAttribute.push({ values: [] });
      // this.selectProductAttributesListValues.push({ values: [] });
      // add product attributes to form
      productAttributes.controls[i] = this.fb.group({
        name: this.fb.group({
          en: ['', Validators.required],
          ar: ['', Validators.required],
        }),
        values: this.fb.array([]),
      });
      // productAttributes.push(
      //   this.fb.group({
      //     name: this.fb.group({
      //       en: ['', Validators.required],
      //       ar: ['', Validators.required],
      //     }),
      //     values: this.fb.array([]),
      //   })
      // );
      productAttributes.controls[i]
        .get('name')
        .get('en')
        .patchValue(attributes[i]['name']['en']);
      productAttributes.controls[i]
        .get('name')
        .get('ar')
        .patchValue(attributes[i]['name']['ar']);

      // list of product attributes values to dropdown fields
      for (
        let valueIndex = 0;
        valueIndex < attributes[i].values.length;
        valueIndex++
      ) {
        this.selectProductAttributesListValues[i].values.push({
          _id: attributes[i].values[valueIndex]['_id'],
          nameEn: attributes[i].values[valueIndex]['en'],
          nameAr: attributes[i].values[valueIndex]['ar'],
        });
      }
    }
  }
