  // End select dropdown

  onSubmit() {
    // Start product attributes
    // adding values to product attributes form array controls
    const productAttributes = this.form.get('productAttributes') as FormArray;
    for (let i = 0; i < productAttributes.length; i++) {
      let productAttributeValues = productAttributes.controls[i].get(
        'values'
      ) as FormArray;
      for (
        let valueIndex = 0;
        valueIndex < this.selectedProductAttribute[i].values.length;
        valueIndex++
      ) {
        productAttributeValues.push(
          this.fb.group({
            en: ['', Validators.required],
            ar: ['', Validators.required],
          })
        );

        productAttributeValues.controls[valueIndex]
          .get('en')
          .patchValue(
            this.selectedProductAttribute[i].values[valueIndex].nameEn
          );

        productAttributeValues.controls[valueIndex]
          .get('ar')
          .patchValue(
            this.selectedProductAttribute[i].values[valueIndex].nameAr
          );
      }
    }
    // adding values to product attributes form array controls
    this.form.get('productAttributes').patchValue(productAttributes.controls);
    // End product attributes

    /////////////////////////
    // Start category
    if (Object.keys(this.selectedCategory).length === 0) {
      this.form.get('category').reset();
    } else {
      var count = 0;
      for (var prop in this.selectedCategory) {
        if (this.selectedCategory.hasOwnProperty(prop)) ++count;
      }
      if (this.selectedCategory[count - 1]) {
        this.form
          .get('category')
          .get('_id')
          .patchValue(this.selectedCategory[count - 1]._id);

        this.form
          .get('category')
          .get('name')
          .get('en')
          .patchValue(this.selectedCategory[count - 1].nameEn);

        this.form
          .get('category')
          .get('name')
          .get('ar')
          .patchValue(this.selectedCategory[count - 1].nameAr);
      }
    }
    // End category
    /////////////////////////

    console.log(this.form.value);
    const formData = new FormData();

    for (const key in this.form.value) {
      if (this.form.value[key] instanceof File) {
        formData.append(key, this.form.value[key]);
      } else {
        // contains object
        formData.append(key, JSON.stringify(this.form.value[key]));
      }
    }
    this.productsService.addOne(formData).subscribe(
      (res) => {
        console.log(res);
        this.image = null;
        this.form.reset();
        this.toastr.info(res['message'], 'Info');
        this.router.navigate(['../']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }
